import { IMG } from '@/config/url'	
import { callAppRouter } from '@/tool/bridge'
import { os } from '@/tool/tool'
export default {
	data () {
		return {
			img: IMG + '/activity/two-festival/bg.png'
		}
	},
	computed: {
		iOS () {
			return os().isPhone
		}
	},
	methods: {
		buy () {
			if (window.bridge) {
				callAppRouter('twoFestivalBuy', null, () => {})
			} else{
				this.$router.push('/app')
			}
		}
	}
}
	



